
<template>
    <div>
        <mt-header title="" fixed class="mt-header wid">
            <router-link to="Myhome" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>

        <div class="ab-tit"></div>
        <div class="card">
            <img src="img/card1.png" alt="">
        </div>
        <hr>
        <!-- <img :src="'upload/'+imgurl" alt=""> -->
        <hr>
        <div class="waring">
            &nbsp;<h4>:</h4>
            <p>&nbsp;&nbsp;</p>
        </div>
        <hr>
        <!-- <form action="uploadFile?did=0"  method="POST" enctype="multipart/form-data" @submit='uploadimg'>
            <input type="file" name="mypic" class="upload" id='img'>
            <br>
            <br>
            <input type="submit" value="" class="submit">
        </form> -->

        <div>
            <input  accept="image/*" type="file" name="mypic" class="upload" id="image">
            <br>
            <input type="button" value="" class="submit" @click="uploadimg">
        </div>
    </div>
</template>
<script>

import {Toast} from 'mint-ui'
export default {
    data(){ return { imgurl:"" } },
    methods:{
        uploadimg(){
            var file = document.getElementById("image").files[0];
            if(file===undefined){
                Toast('')
                return
            }
            var formdata1=new FormData();// form
            formdata1.append('mypic',file,file.name);// appendform,append
            //formdata1.append('img',file);
            let config = {
                headers:{'Content-Type':'multipart/form-data'}
            };  //
            this.axios.post('uploadFile?did=0',formdata1,config).then(result=>{   ///xapi/upimage
                if(result.data.code==1){
                    // this.imgurl=result.data;
                    Toast('')
                    setTimeout(()=>{
                        this.$router.push('Myhome')
                    },1500)
                }
                // console.log(result.data)
            })

        }
    }
}
</script>


<style>
    form{
        padding: 0 10px;
    }
    .submit{
        margin-left: 70%;
    }
    .card>img{
        width: 80%;
        display: block;
        margin: 0 auto;
    }
    .waring{
        padding:0 10px;
    }
    .waring>p{
        color: rgb(165,42,42)
    }
</style>








