<template>
    <div id="app-login">
        <img src="img/login4.png" class="img1">
        <div class="login-item">
            &nbsp;&nbsp;<p>  &nbsp;&nbsp;</p>
            <input type="text" name="phone" v-model="phone" placeholder="" >
        </div>
        <div class="login-item">
            &nbsp;&nbsp;<p>  &nbsp;&nbsp;</p>
            <input type="password" name="logupwd" v-model="logupwd" placeholder="">
        </div>
        <Sec ref="Child1"></Sec>
        <mt-button size="large" style="background:#93d4dc;color:#000" @click="login"></mt-button>            
    </div>
</template>

<script>

import {Toast} from "mint-ui"
import Sec from "@/components/temps/Sec"
export default {
    name:'Login',
    components:{
        Sec
    },
    data(){
        return {
            phone:"",
            logupwd:""
        }
    },
    methods:{
        login(){           
            if(!this.phone){
                Toast("")
                return;
            }

            if(!this.logupwd){
                Toast("")
                return;
            }

            if(this.$refs.Child1.sendMath()==1){
                let url="Login?";
                let postData=this.qs.stringify({
                    phone:this.phone,
                    logupwd:this.logupwd
                });
                this.axios.post(url,postData).then(result=>{
                    if(result.data.code==1){
                        if(result.data.data[0].state==""){
                            //console.log(this.$store.state.uid);
                            if(this.$store.state.uid==result.data.data[0].uid){
                                Toast("")
                            }else{
                                Toast("");
                                sessionStorage.setItem('id',result.data.data[0].uid)
                                setTimeout(()=>{
                                    this.$router.push('Home')
                                },1000)
                            }                            
                        }else{
                            Toast("")
                        }
                    }else{
                        Toast("!")
                    }
                })
            }else{
                Toast("");
            }
        }
    }
}
</script>

<style>
    
</style>




