<template>
    <table>
        <tr>
            <td>
                <h4></h4>
            </td>
            <td class="utd" colspan="2">
                <img :src="'upload/'+uitem.cardurl" alt="">
                <!-- <img src="upload/card1.png" alt=""> -->
            </td>
        </tr>
        <tr v-show="toChild2">
            <td>
                <button @click="checkUser('2')"></button>
            </td>
            <td>
                <button @click="checkUser('1')"></button>
            </td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.uname}}</td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.uid}}</td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.phone}}</td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.state}}</td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.pay}}</td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.logupwd}}</td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.rank}}</td>
        </tr>
        <tr>
            <td></td>
            <td>{{uitem.rtime}}</td>
        </tr>
    </table>
</template>

<script>

import {Toast} from "mint-ui"
export default {
    name:'Ulist',
    props:['toChild','toChild2'],
    data(){
        return {
            Nodo:true,
            uitem:{} //
        }
    },
    created(){
        this.open()
    },
    methods:{
        open(){
            this.uitem=this.toChild
        },
        
        no(){
            this.Nodo=false
        },

        checkUser(a){
            let uid=this.uitem.uid;
            let url=`Checkcard`;
            let card;
            if(a==1){
                card=1
            }else if(a==2){;
                card=2
            }else{
                // 
                this.Nodo=false;
                return
            }
            let postData=this.qs.stringify({
                uid:uid,
                card:card
            })
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    window.location.reload();
                    Toast(result.data.msg)
                }else{
                    Toast('')
                }
            })
        }
    }
}
</script>

<style>
    .utd{
        width: 80%;
    }
    .utd>img{
        width: 100%;
    }
</style>





