<template>
    <div class="succ">
        <mt-header title="" fixed class="mt-header wid">
            <router-link to="Deal" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <h4>{{orderInfo.number}}{{time}}</h4>
        <hr>
        <div class='info'>
            <p> {{orderInfo.price}}</p>
            <p>{{orderInfo.count}}</p>
            <p> {{orderInfo.totle}}</p>
        </div>
        <div class='info' v-show="off.three">
            <p>{{oList.phone}}</p>
            <p>{{oList.uname}}</p>
            <p>
                <u id="link1">{{oList.pay}}</u>
                <br>
                <br>
                <button id="copy" class="bg" @click="btnCopy('1')"></button>
            </p>
        </div>
        <div class='info' v-show="off.four">
            <p>{{orderInfo.phone}}</p>
            <p>{{orderInfo.uname}}</p>
            <p>
                <u id="link2">{{orderInfo.pay}}</u>
                <br>
                <br>
                <button id="copy" class="bg" @click="btnCopy('2')"></button>
            </p>
        </div>
        <div class='info' v-show="off.five"> 
            <p>{{orderInfo.phone}}</p>
            <p>{{orderInfo.uname}}</p>
        </div>
        <div class='info' v-show="off.six"> 
            <p>{{oList.phone}}</p>
            <p>{{oList.uname}}</p>
        </div>

        <hr>
        <div class='info tell' v-show="off.one">
            <h4></h4>
            <br>
            <p></p>
            <p></p>
            <p></p>
        </div>
        <div class='info tell' v-show="off.two">
            <h4></h4>
            <br>
            <p></p>
            <p></p>
            <p></p>
        </div>

        <hr>
        <div v-show='show'>
            <p class="uploadp" v-show="off.one">,</p>
            <p class="uploadp"  v-show="off.two"></p>
            <img :src='dealurl' alt="" class='proveimg' >
        </div>
        <hr>

        <!-- // <form :action='"uploadFile?did="+did'  method="POST" enctype="multipart/form-data" @submit='uploadimg' v-show="off.one">
        //     <input type="file" name="mypic" class="upload" id='img'>
        //     <br>
        //     <br>
        //     <input type="submit" value="" class="submit">
        // </form> -->
        <div v-show="off.one">
            <input  accept="image/*" type="file" name="mypic" class="upload" id="imag">
            <br>
            <input type="button" value="" class="submit" @click="uploadimg">
        </div>
        <hr>

        <div v-show="off.one">
            <!-- <button class="mybtn sbtn" @click='error("1","")'></button> -->
        </div>
        <div v-show="off.two">
           <!-- <button class="mybtn sbtn" @click='error("2","")'></button> -->
            <button class="mybtn sbtn" @click='over'></button>
        </div>
    </div>
</template>

<script>

import {Toast} from "mint-ui"
export default {
    data(){
        return {
            did:this.$store.state.did,
            show:false,
            dealurl:"",
            orderInfo:{},
            time:'',
            oList:{},
            off:{
                one:false,
                two:false,
                three:false,
                four:false,
                five:false,
                six:false
            },
            endtime:'',
        }
    },
    created(){
        this.getList()
    },
    methods:{
        over(){

            this.interval('2');  //
            let url='dealOver';
            let postData4=this.qs.stringify({
                count:this.orderInfo.count*1.15,
                uid:this.orderInfo.uid,
                oid:this.orderInfo.otheruid,
                todo:this.orderInfo.todo
            })
            this.axios.post(url,postData4).then(result=>{
                this.error('4','');
                this.off.two=false
            })
        },

        error(d,t){
            

            let state;
            if(d==1){
                state=''
            }else if(d==2){
                state=''
            }else if(d==3){
                state=''
            }else{
                state='';
                this.endtime=0
            }

            if(this.endtime>0){
                Toast('')
                return
            }
            let postData3=this.qs.stringify({
                did:this.did,
                state:state
            })

            let url='dealError';
            this.axios.post(url,postData3).then(result=>{
                Toast(t)
                this.time=t
            })
        },

        uploadimg(){
            let file1 = document.getElementById("imag").files[0];
            if(file1===undefined){
                Toast('');
                return
            }
            var formdata1=new FormData();// form
            formdata1.append('mypic',file1,file1.name);// appendform,append
            //formdata1.append('img',file);
            let config = {
                headers:{'Content-Type':'multipart/form-data'}
            };  //
            this.axios.post(`uploadFile?did=${this.did}`,formdata1,config).then(result=>{   ///xapi/upimage
                if(result.data.code==1){
                    Toast('')
                    setTimeout(()=>{
                        this.$router.push('Deal')
                    },1500)
                }
            })

        },

        getList(){


            let url='Succeed';
            if(this.$route.query.did){
                this.did=this.$route.query.did
            }else{
                this.did=this.$store.state.did
            }
            let postData=this.qs.stringify({
                did:this.did
            });
            this.axios.post(url,postData).then(result=>{//
                this.getListt(result.data)
            })
        },
        getListt(arr){
            this.orderInfo=arr[0];
            if(this.orderInfo.state==''){
                this.time='';
                this.show=true;
                this.off.two=false;
                this.off.one=false;
                this.off.three=false;
                this.off.four=false;
                this.off.five=false;
                this.off.six=false
            }
            let otherid=this.orderInfo.otheruid;//
            let url1='Self?uid='+otherid;
            this.axios.get(url1).then(result=>{
                this.oList=result.data[0];
                if(this.orderInfo.state==''){
                    // 
                    if(this.orderInfo.uid==sessionStorage.getItem('id')){
                        this.off.two=false;
                        this.off.one=true;
                        this.off.three=true;
                        this.off.four=false;
                        this.off.five=false;
                        this.off.six=false
                    }else if(otherid==sessionStorage.getItem('id')){
                        this.off.one=false;
                        this.off.two=true;
                        this.off.three=false;
                        this.off.four=false;
                        this.off.five=true;
                        this.off.six=false
                    }
                }else if(this.orderInfo.state==''){
                    if(this.orderInfo.uid==sessionStorage.getItem('id')){
                        this.off.one=false;
                        this.off.two=true;
                        this.off.three=false;
                        this.off.four=false;
                        this.off.five=false;
                        this.off.six=true;
                    }else if(otherid==sessionStorage.getItem('id')){
                        this.off.one=true;
                        this.off.two=false;
                        this.off.three=false;
                        this.off.four=true;
                        this.off.five=false;
                        this.off.six=false
                    }
                }
            })

            this.dealurl=this.orderInfo.dealurl;
            if(this.dealurl!=''){
                this.show=true
                this.dealurl=`upload/${this.dealurl}`
            }else{
                this.show=false
                this.dealurl='img/menu1.png'
            }

            this.interval('1')
        },

        interval(y){
            let tt;
            let t=setInterval(()=>{
                tt=Number((3600-parseInt((new Date().getTime()-new Date(this.orderInfo.matching).getTime())/1000))/60).toFixed(2)
                this.time=` ${tt} `;
                this.endtime=tt;
                if(tt<0){
                    clearInterval(t);
                    this.error("3","")
                    this.time='!'
                }
                if(y==2){
                    clearInterval(t);
                }
            },1000)
        },

        btnCopy(r) { //
            let url;
            if(r==1){
                url = document.getElementById("link1").innerText;
            }else{
                url = document.getElementById("link2").innerText;
            }
            var input = document.createElement("input");
            input.value = url;
            document.body.appendChild(input);
            input.select(); // 
            document.execCommand("Copy"); // 
            input.style.display = "none";
            Toast("");
        }
    }    
}
</script>

<style>
    .succ>h4{
        padding: 10px;
        margin: 10px auto;
    }
    .info{
        padding:1% 3%; 
    }
    .info>p{
        /* color: rgb(165,42,42); */
        color: rgb(12, 97, 131);
        font-size: 17px;
        font-weight: 700
    }
    .info>p>u{
        color: rgb(0, 0, 0) !important;
        font-size: 18px !important
    }
    .tell>p{
        font-size: 14px;

    }
    .proveimg{
        display: block;
        margin: 0 auto;
        width: 80%;
    }
    .uploadp{
        margin-left: 20%;
        color: rgb(12, 97, 131);
        font-size: 19px
    }
    .sbtn{
        margin-left: 20%;
    }
</style>





