import Vue from 'vue'
import App from './App.vue'
import router from './router'

import'mint-ui/lib/style.css'
// import './lib/mui/css/style.css'

Vue.config.productionTip = false


//vuex
// import store from './store'


//0.1  vuex
import Vuex from "vuex"
Vue.use(Vuex)
//0.2store
var store=new Vuex.Store({
  //
  state:{
    uid:'',
    depo:'',

    mid:"",//
    did:"",//
    
    Login:true,//
    Reg:false,
    freez:0.00,
    
    // cartCount:sessionStorage.getItem("cartCount")||0
  },
  //
  mutations:{
    // increment(state){
    //   state.cartCount++;
    //   sessionStorage.setItem("cartCount",state.cartCount);
    // },
    newNum(state,sum){ //
      state.freez=sum;
    },
    login(state,sum){ //
      state.Login=sum;
    },
    reg(state,sum){ //
      state.Reg=sum;
    },
    deposit(state,sum){
      state.depo=sum;
    },
    Mid(state,sum){ 
      state.mid=sum;
    },
    Did(state,sum){ 
      state.did=sum;
    }
  },
  //
  getters:{
    optCartCount:function(state){
      return state.cartCount;
    }
  }
})
//0.3storevue

//5:  
//Vue.http.options.root = "http://127.0.0.1/vue_ser/";
//6:post  application/x-www-form-urlencoded
//Vue.http.options.emulateJSON = true;
//  MUI   Bootstrap 
import './lib/mui/css/mui.css'
//  MUI 
// 
import './lib/mui/css/icons-extra.css'
import './lib/mui/css/style.css'

//1:Header
import {Header,Button} from "mint-ui"
//2Header
Vue.component(Header.name,Header);
Vue.component(Button.name,Button);

//axiospost qs
import qs from "qs"
Vue.prototype.qs=qs;


//
import {Swipe,SwipeItem} from "mint-ui"
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name,SwipeItem);

import axios from "axios"
//main.jssession
axios.defaults.withCredentials=true;
//mian.jsaxiosvue
Vue.prototype.axios=axios;

//
Vue.filter("dataFilter",function(value){
  //1 
  var now=new Date(value);
  // //2    
  var y=now.getFullYear();
  var m=now.getMonth()+1;
  var d=now.getDate();
  // //2    
  // var h=now.getHours();
  // var mi=now.getMinutes();
  // var s=now.getSeconds();
  // //4  10 0 
  // // if(m<10 || d<10){m="0"+m;d="0"+d};
  // //m<10&&(m="0"+m);
  // //5 
  return y+"-"+m+"-"+d;
  // let a=value.substring(5,11)

  // return a

})

Vue.filter("timeFilter",function(value){
  var now=new Date(value);
  // //2    
  var m=now.getMonth()+1;
  var d=now.getDate();
  // //2    
  var h=now.getHours();
  var mi=now.getMinutes();
  return m+"/"+d+" "+d+':'+mi;

})


//
router.beforeEach((to, from, next) => {
  let LOGIN = sessionStorage.getItem('id');
  let RO=window.sessionStorage.getItem('data');
  // console.log(RO+'///'+1111)
  if (to.path === '/Log') {
    sessionStorage.removeItem('Log') 
  }
  
  if (!LOGIN && to.path!== '/Log') {
    next({ path:  '/Log' })
  } else {
     if (!RO && to.path== '/Uinfo') {
        next({ path:  '/Shuang' })
      } else if (!RO && to.path== '/Ulist'){
        next({ path:  '/Shuang' })
      } else if (!RO && to.path== '/Bulltext'){
        next({ path:  '/Shuang' })
      } else if (!RO && to.path== '/Wrong'){
        next({ path:  '/Shuang' })
      } else if (!RO && to.path== '/WrongInfo'){
        next({ path:  '/Shuang' })
      } else {
        next()
      } 
  }
})



new Vue({
  router,
  render: h => h(App),
  store //storeVue
}).$mount('#app')



