<template>
    <div>
        <mt-header title="" fixed class="mt-header wid">
            <router-link to="Myhome" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> 
        <div>
            <h4 class="ref-h4">&nbsp;&nbsp; </h4>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <router-link to='Reg?boss=15036368031' id="link"> http://bitoc.applinzi.com/#/Log?boss={{phone}}</router-link>
            <br>
            <button id="copy" class="bg" @click="btnCopy"></button>
        </div>
        <div class="ab-tit"> </div>
        <div class="ab-d1">
            <p class="ab-p">
                BOC  
                <span class="ab-span"> 
                    <br>
                    1  
                    <br>
                    <br>
                    2  
                    <br>  
                    <br>  
                    3  
                    <br>
                    <br>
                    4</span>
            </p>
        </div>    
        <!-- <upload :uploadType="`head`" :imgWidth="`85px`" :imgHeight="`104px`" :imgUrl="imgUrl"
        @upload="getImgUrl"></upload> -->
    </div>
</template>

<script>
import {Toast} from "mint-ui"
export default {
    components:{
    },
    data(){
        return {
            phone:''
        }
    },
    created(){
        this.self()
    },
    methods:{
        btnCopy() {
            var url = document.getElementById("link").innerText;
            var input = document.createElement("input");
            input.value = url;
            document.body.appendChild(input);
            input.select(); // 
            document.execCommand("Copy"); // 
            input.style.display = "none";
            Toast("");
        },
        self(){
            let url="Self";
            this.axios.get(url).then(result=>{
                this.phone=result.data[0].phone;   
            })
        }
    }
}
</script>

<style>
    .ref-h4{
        margin: 15px 0;
    }
    #link{
        font-size: 10px;
        color: rgb(0, 0, 0);
    }
    #copy{
        margin-left: 60%;
    }
</style>





