<template>
    <div>
         <mt-header title="" fixed  class="mt-header wid"></mt-header>
         <Footer></Footer>
         <div class="deal-tit"> {{pic}}</div>
         <div class="deal-btn">
             <button class="btn-buy" :style="{background:(btn.one?'#aae2de':'')}" @click="deal('one')" data-id="1"></button>
             <button class="btn-sell" :style="{background:(btn.two?'#aae2de':'')}" @click="deal('two')" data-id="2"></button>
         </div>
         <hr>
         <div class="deal-form">
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="text" name="count" v-model="count" placeholder="0~200">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="text" name="price" v-model="price" placeholder="">
            </div>
            <button class="deal-btn1" :data-id='0' @click="Adeal($event,'1')">{{submit}}</button>
         </div>
         <hr>
         <div v-show='btn.four'>
             <h4>:</h4>
             <table>
                <tr class="tr1 bg">
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
                <tr v-for='(item,i) of list' :key='i'>
                    <td>{{item.number}}</td>
                    <td>{{item.price}}</td>
                    <td>{{item.count}}</td>
                    <td>{{item.totle}}</td>
                    <td>{{item.matching | timeFilter}}</td>
                    <td>{{item.state}}</td>
                    <td>
                        <button class="btn" :data-id='item.did' @click='lookdeal'></button>
                    </td>
                </tr>
            </table>
         </div>
         <div v-show="btn.two">
            <h4>:</h4>
            <table>
                <tr class="tr1 bg">
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
                <tr>
                    <td colspan="6">
                        <input type="text" placeholder="" v-model="search" class="search-input">
                        <button class="search-btn" @click='searchList'></button>
                    </td>
                </tr>
                <tr v-for="(item,i) of buyList" :key='i'>
                    <td>{{item.number}}</td>
                    <td>{{item.price}}</td>
                    <td>{{item.count}}</td>
                    <td>{{item.totle}}</td>
                    <td>{{item.state}}</td>
                    <td>
                        <button class="btn" :data-id='item.did'  :data-uid='item.uid' :data-count='item.count' @click='Adeal($event,"2")'></button>
                    </td>
                </tr>
            </table>
         </div>
         <div v-show="btn.one">
            <h4>:</h4>
            <table>
                <tr class="tr1 bg">
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
                <tr>
                    <td colspan="6">
                        <input type="text" placeholder="" v-model="search" class="search-input">
                        <button class="search-btn" @click='searchList'></button>
                    </td>
                </tr>
                <tr v-for="(item,i) of sellList" :key="i">
                    <td>{{item.number}}</td>
                    <td>{{item.price}}</td>
                    <td>{{item.count}}</td>
                    <td>{{item.totle}}</td>
                    <td>{{item.state}}</td>
                    <td>
                        <button class="btn" :data-id='item.did' :data-uid='item.uid' @click='Adeal($event,"2")'></button>
                    </td>
                </tr>
            </table>
         </div>         
         <div v-show='btn.three'>
             <table>
                <tr class="tr1 bg">
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
                <tr>
                    <td>{{orderList.number}}</td>
                    <td>{{orderList.price}}</td>
                    <td>{{orderList.count}}</td>
                    <td>{{orderList.totle}}</td>
                    <td>{{orderList.state}}</td>
                    <td>
                        <button class="btn" :data-id='orderList.did' @click='Adeal($event,"2")'></button>
                    </td>
                </tr>
            </table>
         </div>
         <div class='affirm' v-if="aff==1">
             <input type="password" placeholder="" v-model="dealupwd">
             <hr>
             <button @click='cancel'></button>
             <button class='mybtn' @click="affirmupwd"></button>
         </div>
         <div class='affirm' v-else-if="aff==2">
             <p></p>
             <p></p>
             <hr>
             <button @click='cancel'></button>
             <button class='mybtn' @click='affirmdeal'></button>
         </div>
    </div>
</template>

<script>

import {Toast} from "mint-ui"
import Footer from "@/components/Footer"
export default {
    components:{
        Footer
    },
    data(){
        return {
            pic:'',
            count:'', //
            price:'', //
            search:'', //
            deposit:'', // 
            dealupwd:'',
            did:'',
            aff:0, //
            btn:{
                one:true,
                two:false,
                three:false,
                four:false
            },
            submit:"",
            buyList:[],
            sellList:[],
            orderList:{}, //
            list:[],
            buycount:'' ,//
            rank:''
        }
    },
    created(){
        this.getuser()
        this.getDealb()
        this.getDeals()
        this.nowpic()
        this.getme()
    },
    methods:{
        Adeal(e,n){
            if(this.rank==''){
                Toast('')
                return
            }

            let id=e.target.dataset.id;
            let uid=e.target.dataset.uid;
            // console.log(typeof(e.target.dataset.count))
            if(e.target.dataset.count){
                // console.log(123)
                this.buycount=Number(e.target.dataset.count);
                if(Number(e.target.dataset.count)>this.deposit){
                    Toast('')
                    return
                }
            }
            if(uid==sessionStorage.getItem('id')){
                Toast('')
                return
            }
            this.aff=n;
            this.$store.commit('Did',id)
            this.did=this.$store.state.did;
        },

        lookdeal(e){
            let id=e.target.dataset.id;
            this.$store.commit('Did',id)
            this.$router.push('Succeedb')
        },

        getme(){
            let url='Getdealing';
            this.axios.get(url).then(result=>{
                if(result.data.length>0){
                    this.list=result.data
                    this.btn.four=true
                }else{
                    this.btn.four=false
                }
            })
        },

        affirmdeal(){
            let time=new Date().getHours();
            if(time<9 || time>20){
                Toast('9:00~21:00')
                return
            }

            this.aff=0
            let url='GetOrderInfo';
            let postData=this.qs.stringify({
                did:this.did,
                otheruid:sessionStorage.getItem('id'),
                count:this.buycount
            });
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    Toast('')
                    this.$router.push('Succeedb')
                }else{
                    Toast('')
                }
            })
        },

        cancel(){//
            this.aff=0
        },

        affirmupwd(){ //
            if(!this.dealupwd){
                Toast('')
                return
            }

            let url='Dupwd';
            let postData=this.qs.stringify({
                dealupwd:this.dealupwd
            })
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    this.dealupwd=''
                    this.aff=0;
                    this.applyDeal()
                }else{
                    Toast(result.data.msg)
                }
            })
        },
        //this.$store.commit('newNum',6)
        getuser(){
            let url='Self';
            this.axios.get(url).then(result=>{
                this.rank=result.data[0].rank;
                this.$store.commit('deposit',result.data[0].deposit);
                this.deposit=this.$store.state.depo;
            })
        },

        nowpic(){
            //
            let url='Pic';
            this.axios.get(url).then(result=>{
                this.pic=result.data
            })
        },
        
        deal(eee){
            this.btn.one=false;
            this.btn.two=false;
            this.btn.three=false;
            this.btn[eee]=true;
            if(this.btn.one==false){
                this.submit=""
            }else{
                this.submit=""
            }
        },

        applyDeal(){
            
            let count=parseInt(this.count);
            if(this.submit==''){
                if(count>this.deposit*0.85){
                    Toast('!')
                    return
                }
            }
            
            if(count<1 || count>200){
                Toast('1~200')
                return
            }

            let time=new Date().getHours();
            if(time<9 || time>20){
                Toast('9:00~21:00')
                return
            }

            if(!count){
                Toast('');
                return
            }

            if(!this.price){
                Toast('');
                return
            }

            if(this.price>this.pic || this.price<(this.pic-0.3)){
                Toast('')
                return
            }
            
            let url='Applydeal?';
            let postData=this.qs.stringify({
                count:count,
                price:Math.floor(this.price*100)/100,
                state:this.submit,
                totle:Math.floor(count*this.price*100)/100
            })
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    Toast('')
                    // this.$store.commit('deposit',this.deposit-this.count);
                    this.getDealb()
                    this.getDeals()
                    this.count=''
                    this.price=''
                }else{
                    Toast('')
                }
            })
        },

        getDealb(){//
            let url='GetBuyDealList';
            this.axios.get(url).then(result=>{
                this.buyList=result.data
            })
        },
        
        getDeals(){//
            let url='GetSellDealList';
            this.axios.get(url).then(result=>{
                this.sellList=result.data
            })
        },

        searchList(){//
            if(1){
                Toast('')
                return
            }
            
            if(!this.search){
                Toast('')
                return
            }

            let url='GetOrder?number='+this.search;
            this.axios.get(url).then(result=>{
                if(result.data.length==0){
                    Toast('')
                }else{
                    this.deal('three')
                    this.orderList=result.data[0]
                    this.search=''
                }
            })
        }
    }
}
</script>

<style>
    .affirm{
        width: 300px;
        height: 160px;
        background: rgba(0,0,0,0.6);
        padding: 20px;
        position: absolute;
        border-radius: 20px;
        left: 50%;
        margin-left: -150px;
        top: 50%;
        margin-top: -80px;
        z-index: 100
    }
    .affirm>p{
        color: rgb(235,235,235);
        font-size: 18px !important;
        font-weight: 500
    }
    .affirm>input{
        font-size: 15px
    }
    .affirm>button{
        margin-left: 20%;
    }
    .deal-tit{
        text-align: center;
        background: rgb(170, 226, 222);
        font-weight:600;
        padding: 10px;
        margin:10px 0;
    }
    .deal-btn{
        display: flex;
        justify-content: space-around;
        margin: 20px;
    }
    .btn-buy,.btn-sell{
        padding:5px 50px;
    }
    .deal-btn1{
        margin-left: 80%;
        background: rgb(170, 226, 222);
    }
    .deal-form{
        padding: 5px 20px;
        margin: 10px 0;
    }
    .bg{
        background: rgb(170, 226, 222) !important;
    }
    .search-input{
        width: 74% !important;
        border-radius: 20px !important;
        margin-bottom: 0 !important;
        margin: 10px 0;
        border: 2px solid rgb(170, 226, 222) !important;
    }
    .search-btn{
        width: 24%;
        border-radius: 20px !important;
        background: rgb(170, 226, 222);
        margin: 15px 0;
    }
</style>



