<template>
    <table>
        <tr>
            <td></td>
            <td colspan="2">{{list.did}}</td>
        </tr>
        <tr>
            <td>uid</td>
            <td colspan="2">{{list.uid}}</td>
        </tr>
        <tr>
            <td>oid</td>
            <td colspan="2">{{list.otheruid}}</td>
        </tr>
        <tr>
            <td></td>
            <td colspan="2">{{list.state}}</td>
        </tr>
        <tr>
            <td></td>
            <td colspan="2">
                <img :src='"upload"+list.dealurl' style='width:100%'>
            </td>
        </tr>
        <tr>
            <td></td>
            <td colspan="2">{{list.matching | timeFilter}}</td>
        </tr>
        <tr>
            <td></td>
            <td colspan="2">{{list.todo}}</td>
        </tr>
        <hr>
        <tr>
            <td>
                <button @click='force'></button>
            </td>
            <td>
                <button @click='recover(""+list.todo)'></button>
            </td>
        </tr>
    </table>
</template>

<script>
import {Toast} from 'mint-ui'
export default {
    data(){
        return {
            list:{},
            did:this.$store.state.did
        }
    },
    created(){
        this.getWrongInfo()
    },
    methods:{
        getWrongInfo(){
            let url='Succeed';
            let postData=this.qs.stringify({
                did:this.did
            });
            this.axios.post(url,postData).then(result=>{
                this.list=result.data[0]
            })
        },
        force(){
            let url='dealOver';
            let postData4=this.qs.stringify({
                count:this.list.count*1.15,
                uid:this.list.uid,
                oid:this.list.otheruid,
                todo:this.list.todo
            })
            this.axios.post(url,postData4).then(result=>{
                this.recover('')
            })
        },
        recover(val){
            let url1='dealError'
            let postData=this.qs.stringify({
                did:this.did,
                state:val
            })
            this.axios.post(url1,postData).then(result=>{
                if(result.data.code==1){
                    Toast('')
                }
            })
        }
    }
}
</script>




