<template>
    <div>
        <mt-header title="" fixed class="mt-header wid"></mt-header>
        <div class="reg">
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="text" name="phone" v-model="phone" placeholder="" @blur="searchPhone">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p>  &nbsp;&nbsp;</p>
                <input type="text" name="uname" v-model="uname" placeholder="">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="text" name="pay" v-model="pay" placeholder="">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="password" name="logupwd1" v-model="logupwd1" placeholder="6~12">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="password" name="logupwd2" v-model="logupwd2" placeholder="">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="password" name="dealupwd1" v-model="dealupwd1" placeholder="6">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p> </p>
                <input type="password" name="dealupwd2" v-model="dealupwd2" placeholder="">
            </div>
            <div class="login-item">
                &nbsp;&nbsp;<p>  </p>
                <input type="text" name="boss" v-model="boss" placeholder="">
            </div>
            <Sec ref="Child"></Sec>
        </div>
        <hr>
        <div class="agree">
            <h4></h4>
            <br>
            <span>
                 18~70
            </span>
            <br>
            <br>
            <span>
                 
            </span>
            <br>
            <br>
            <span>
                 
            </span>
            <br>
            <br>
            <div>
                <input type="checkbox" v-model="checked">
                <span class="check"> BOC</span>
            </div>
        </div>
        <hr>
        <br>
        <div>
            <mt-button size="large" style="background:#93d4dc;color:#000" @click="reg"></mt-button>
        </div>
    </div>
</template>

<script>

import {Toast} from "mint-ui"
import Sec from "@/components/temps/Sec"
export default {
    name:'Reg',
    components:{
        Sec
    },
    props:['sendChild'],
    data(){
        return {
            phone:"",
            uname:"",
            pay:"",
            logupwd1:"",
            logupwd2:"",
            dealupwd1:"",
            dealupwd2:"",
            boss:'',
            security:'',
            math:'',
            boss:'',
            checked:""
            
            // this.$route.query.boss
        }
    },
    created(){
        if(this.sendChild){
            this.boss=this.sendChild
        }
    },
    methods:{

        searchPhone(){
            let url="Searchphone?phone="+this.phone;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    Toast("!");
                    this.phone=""
                }
            })
        },

        reg(){
            
            let reg1=/^1[3-8][0-9]{9}$/;
            if(!reg1.test(this.phone)){
                Toast("");
                return;
            }

            let reg2=/^[\u4E00-\u9FA5]{2,4}$/;
            if(!reg2.test(this.uname)){
                Toast(",2~4");
                return;
            }

            let reg3=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,12}$/;
            if(!reg3.test(this.logupwd1)){
                Toast("")
                return;
            }
            if(this.logupwd2!=this.logupwd1){
                Toast("");
                return;
            }

            let reg4=/^[0-9]{6}$/;
            if(!reg4.test(this.dealupwd1)){
                Toast("");
                return;
            }
            if(this.dealupwd1!=this.dealupwd1){
                Toast("");
                return;
            }

            if(this.phone==this.boss){
                Toast('');
                return
            }

            if(this.boss==''){
                Toast('');
                return
            }

            if(this.security!=this.math){
                Toast("");
                return;
            }

            if(!this.checked){
                Toast("");
                return
            }

            let postData=this.qs.stringify({
                phone:this.phone,
                uname:this.uname,
                pay:this.pay,
                logupwd:this.logupwd1,
                dealupwd:this.dealupwd1,
                boss:this.boss
            });
            
            if(this.$refs.Child.sendMath()==1){
                let url="Reg?"
                this.axios.post(url,postData).then(result=>{
                    if(result.data.code==1){
                        Toast("");
                        setTimeout(()=>{
                            this.$store.commit('login',true)
                            this.$store.commit('reg',false)
                        },2000)
                    }
                })
            }else{
                Toast("")
            }
        }
    }
}
</script>

<style>
    .reg{
        margin-top: 20px;
    }
  
    .load{
        display:block !important;
        margin-left:35% !important;
    }
    .agree{
        padding: 5px;
    }
    .agree span{
        color: rgb(75,75,75);
        font-size: 12px;
    }
    .check{
        color: rgb(255,0,0) !important;
        font-weight: 700;
        font-size: 14px !important;
    }
</style>





